

$(function(){

  //ÎÇòÏ§ëÏóê Î∞îÍøÄ Í≤É: Ïä§ÌÅ¨Î°§ÌÉë 0ÏùºÎïåÎßå Ìà¨Î™ÖÎ∞∞Í≤Ω ÏïÑÎãêÎïåÎäî Ìù∞Î∞∞Í≤Ω
  //Ïä§ÌÅ¨Î°§ ÏóÖ/Îã§Ïö¥ Îî∞ÎùºÏÑú Î©îÎâ¥ Ïò®Ïò§ÌîÑ

  $(window).scroll(function(){
    let p=$(this).scrollTop()
    if(p===0){
      $('.header').addClass('top')
    } else{$('.header').removeClass('top')}
  })




  $('.gnb ul>li').hover(function(){
    $(this).children('.gnb_hover').stop().fadeIn(300)
    $('.header').removeClass('top')
    },function(){
      $(this).children('.gnb_hover').stop().fadeOut(300)
      $('.header').addClass('top')
    })



})////jquery_END






